html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline
}

* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
}

body {
  font: 16px/24px Arial, sans-serif;
  font-weight: 400;
  color: #222;
}

ul,
ol,
li {
  list-style: none
}

a,
a:hover {
  text-decoration: none;
  cursor: pointer
}

.wrap{
  position: relative;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.wrap-bg{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 0;
  filter: saturate(0.5);
}

.container{
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.h2, .h3{
  font: 32px/44px Arial, sans-serif;
  font-weight: bold;
  text-transform: capitalize;
  margin-bottom: 30px;

  @media only screen and (max-width: 576px) {
    margin-bottom: 16px;
}
}
.h3{
  font-size: 24px;
  line-height: 32px;
}